<template>
  <v-container>
    <v-dialog v-model="dialog" persistent max-width="400px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>

        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex
                xs12
                sm12
                md12
                d-flex
              >
                <v-select
                  v-model="selectedReservation"
                  :items="reservations"
                  item-text="listingName"
                  item-value="selectedReservation"
                  return-object
                  v-on:change="chooseReservation"
                  label="Reservations"
                ></v-select>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>

      </v-card>
    </v-dialog>
    <v-layout
      text-xs-center
      wrap
    >

      <v-flex xs12>
        <!-- <amplify-s3-image :imagePath="imagePath" /> -->
        <h1 class="display-2 font-weight-bold mb-3">
          {{ $t("message.hello") }} {{ username }}
        </h1>
        <h2>width: {{ width }}</h2>
        <h2>height: {{ height }}</h2>
        <p class="subheading font-weight-regular">
          <br>
          <router-link to="/checkIn">Check-in</router-link>
          <br>
          <router-link to="/preCheckIn">Pre Check-in</router-link>
          <br>
          <v-btn @click.native="signOut">
            signOut
          </v-btn>
        </p>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import { CallDialog, Exceptions } from '@/utils'
import store from '@/store'

export default {
  data() {
    return {
      imagePath: 'face/0.jpg',
      dialog: false,
      selectedReservation: null
    }
  },
  
  created () {
    this.dialog = true
  },

  async beforeRouteEnter (to, from, next) {
    const error = await store.dispatch('checkIn/loadReservations').catch(error => {
      console.error(error)
      return error
    })

    if (error) {
      CallDialog(error)
      return next(false)
    } else {
      if (store.state.checkIn.reservations.length > 0) {
        return next()
      } else {
        CallDialog(Exceptions.CustomNoReservation)
        return next(false)
      }
    }
  },
  async beforeRouteUpdate (to, from, next) {
    const error = await store.dispatch('checkIn/loadReservations').catch(error => {
      console.error(error)
      return error
    })

    if (error) {
      CallDialog(error)
      return next(false)
    } else {
      if (store.state.checkIn.reservations.length > 0) {
        return next()
      } else {
        CallDialog(Exceptions.CustomNoReservation)
        return next(false)
      }
    }
  },
  computed: {
    formTitle () {
      return 'Select A Reservation'
    },
    username: {
      get() {
        return this.$store.state.session.username
      }
    },
    width: {
      get() {
        // return document.documentElement.clientWidth
        return this.$window.width
      }
    },
    height: {
      get() {
        // return document.documentElement.clientHeight
        return this.$window.height
      }
    },
    ...mapState({
      reservations: state => state.checkIn.reservations,
    }),
  },
  methods: {

    async signOut () {
      this.$store.dispatch('session/logout')
        .catch(e => console.log('logout!!!', e))
    },
    async chooseReservation(selectedReservation) {
      console.log('selectedReservation', selectedReservation)

      const error = await this.loadReservation(selectedReservation).catch(error => {
        console.error(error)
        return error
      })

      if (error) {
        CallDialog(error)
        this.dialog = false
        return
      }

      const saveError = await this.saveIdentityId().catch(error => {
        console.error(error)
        return error
      })
        
      if (saveError) {
        CallDialog(saveError)
        this.dialog = false
        return
      }

      this.dialog = false
      
    },
    ...mapActions({
      loadReservations: 'checkIn/loadReservations',
      loadReservation: 'checkIn/loadReservation',
      saveIdentityId: 'checkIn/saveIdentityId'
    })
  },
}
</script>

